.a{
    padding: 50px 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    
    
}
.a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
  }

.a-left{
   
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    flex: 1;
}
.a-right
{
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    flex:1;
    
}


.a-about
{
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-top: 100px;
}


.a-img-1
{
    width: 100%;
  height: 100%;
  object-fit: cover;
   position: relative;
    border-radius: 10%;
}
.a-title
{
    margin-top: 50px;
    font-size: 40px;
}
.a-desc
{
    font-size: 30px;
    margin-right: 30px;
    font-weight: bold;
    text-align: justify;
}
.a-desc-1
{
    font-size: 30px;
