.i{
    display: flex;
    align-items: stretch;
   background-color: #292b36;
}
.i-left{
width: 15vw;
flex: auto;
justify-content: center;
margin-top: 300px;

}
.i-middle
{
    justify-content: center;    
    margin-top: 200px;
    flex: auto;
    margin-bottom: 300px;
    margin-left: 100px;
}
.i-right{
flex: auto;
position: relative;

}


.i-left-wrapper{
    padding: 50px;
    display: flex;
    height: 50%;
    flex-direction: column;
    justify-content: space-between;

}
.i-intro{
    font-size: 60px;
   font-weight: bold;
   
   
   justify-content: center;
   align-self: start;
}

.i-title{
    height: 50px;
    overflow: hidden;
    
}
.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    50%{
        transform: translateY(-50px);
    }
    75%{
        transform: translateY(-100px);
    }
    100%{
        transform: translateY(-150px);
    }
    
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;
    
}
.i-img{
    width: 100%;
    height: 60%;
    position: relative;
    border-radius: 50%;
    align-content: center;
    margin-top:50px;
    margin-left: 10px;
    border-width: 20px;
    border-color: #000;
    background-color: #edeafc;
    
  /*  transform: rotate(-8deg);*/
   
}

.i-desc{
    font-size: 30px;
    font-weight: bold;
    color:white;
    
}
.icon
{
    
    color:white;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-top: 20px;
    margin-left: 80px;
    
}

.i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    flex-direction: column;
    bottom: 20px;
    margin-bottom: 200px;
  }
  .scroll
  {
      flex-direction: column;
  }
  .i-intro{
      color:white;
  }