.NavbarItems{

    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0%;
}
.nav-menu
{
    display: grid;
    grid-template-columns: repeat(7,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}
.nav-links{
    color: black;
    text-decoration: none;
    padding: 0.5rem 1rem;
}
.nav-links:hover{
    background-color: #6df76d;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}
.fa-bars{
    color: white;
}
.scrolled{
    position: fixed;
    top: 0;
    left: 0;
    background-color: lightblue;
  }
.ss
{
    color: black;
    text-decoration: none;
}