.pl {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #292b36;
  }
  
  .pl-texts {
    width: 65%;
    color: white;
  }
  
  .pl-title {
    font-size: 50px;
    font-weight: 600;
    color: white;
  }
  
  .pl-desc {
    margin: 20px 0px;
    color: white;
  }
  
  .pl-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  @media screen and (max-width: 480px) {
    .pl {
      padding: 10px;
    }
  
    .pl-texts {
      width: 100%;
    }
  
    .pl-desc {
      display: none;
    }
  }
  .bb
  {
    border-radius: 10%;
    color: black;
    font-size: large;
  }
  .bb-a
  {
    text-decoration: none;
    color: white;
  }